.background {
    width: 100vw;
    height: 100vh;
    color: #fff;
    background: -webkit-gradient(linear, right top, left top, from(#EE7752), color-stop(#E73C7E), color-stop(#23A6D5), to(#23D5AB));
    background: -webkit-linear-gradient(right, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background: -o-linear-gradient(right, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background: linear-gradient(-90deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
    position: absolute;
    margin: 0 auto;
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}

@keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}



